.video {
  align-self: stretch;
  height: 560px;
  position: relative;
}

.matterhorn-popup {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 900px;
  max-width: 90%;
  max-height: 90%;
  padding: 20px;
  display: flex;
  position: relative;
  overflow: auto;
}

@media screen and (width <= 650px) {
  .video {
    height: 50.625vw !important;
  }

  .matterhorn-popup {
    box-sizing: border-box;
    padding: 10px;
  }
}

